#pp-nav {
  position: fixed;
  z-index: 100;
  margin-top: -32px;
  top: 50%;
  opacity: 1;
}

#pp-nav.right {
  right: 17px;
}

#pp-nav.left {
  left: 17px;
}

.pp-section.pp-table {
  display: table;
}

.pp-tableCell {
  display: table-cell;
  vertical-align: middle;
  width: 100%;
  height: 100%;
}

.pp-slidesNav {
  position: absolute;
  z-index: 4;
  left: 50%;
  opacity: 1;
}

.pp-slidesNav.bottom {
  bottom: 17px;
}

.pp-slidesNav.top {
  top: 17px;
}

#pp-nav ul,
.pp-slidesNav ul {
  margin: 0;
  padding: 0;
}

#pp-nav li,
.pp-slidesNav li {
  display: block;
  width: 14px;
  height: 13px;
  margin: 7px;
  position: relative;
}

.pp-slidesNav li {
  display: inline-block;
}

#pp-nav li a,
.pp-slidesNav li a {
  display: block;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  cursor: pointer;
  text-decoration: none;
}

#pp-nav li .active span,
.pp-slidesNav .active span {
  background: #333;
}

#pp-nav span,
.pp-slidesNav span {
  top: 2px;
  left: 2px;
  width: 8px;
  height: 8px;
  border: 1px solid #000;
  background: rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  z-index: 1;
}

.pp-tooltip {
  position: absolute;
  top: -2px;
  color: #fff;
  font-size: 14px;
  font-family: arial, helvetica, sans-serif;
  white-space: nowrap;
  max-width: 220px;
}

.pp-tooltip.right {
  right: 20px;
}

.pp-tooltip.left {
  left: 20px;
}

.pp-scrollable {
  overflow-y: scroll;
  height: 100%;
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Cera Round Pro", "Roboto", sans-serif;
  overflow-x: hidden;
}

button {
  cursor: pointer;
  padding: 0;
  border: none;
  background: transparent;
  outline: none;
}

input:not([type="range"]),
select,
textarea {
  border: none;
  outline: none;
  transition: 0.3s ease;
}

input:not([type="range"]):focus,
select:focus,
textarea:focus {
  box-shadow: 0 0 0 3px #042a8c;
}

.overflow-hidden {
  overflow: hidden;
}

.even-logo {
  display: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
  max-width: 100%;
}

.basic-flex {
  display: flex;
  align-items: center;
}

.img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.img-wrapper img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

.side-nav {
  padding: 45px 0;
  width: 140px;
  background-color: #fff;
  transition: 0.7s ease;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.social-links {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.circle {
  display: block;
  position: absolute;
  border-radius: 50%;
}

.circle.primary {
  background-color: #ed0677;
}

.circle.secondary {
  background-color: #14b1e7;
}

.circle.blurred {
  filter: blur(15px);
}

.social-links__item {
  text-align: center;
}

.social-links__item:not(:last-child) {
  margin-bottom: 13px;
}

.social-links__item a {
  display: block;
  width: 43px;
  height: 43px;
  line-height: 43px;
  border-radius: 50%;
  font-size: 13px;
  font-weight: 900;
  color: #042a8c;
  border: 1px solid #042a8c;
}

.black-line .line-col {
  background-color: rgba(0, 0, 0, 0.05);
}

.line-col {
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000;
  height: 100%;
  position: fixed;
  top: 0;
  user-select: none;
  pointer-events: none;
  left: 25%;
  width: 1px;
  background-color: rgba(255, 255, 255, 0.05);
  z-index: 10;
  transition: 0.7s ease;
  display: none;
}

.line-col:nth-child(2) {
  left: 40%;
}

.line-col:nth-child(3) {
  left: 55%;
}

.line-col:nth-child(4) {
  left: 70%;
}

.line-col:nth-child(5) {
  left: 85%;
}

.main {
  display: flex;
}

.main.even-section .side-nav {
  background-color: #14b1e7;
}

.main.even-section .slide-controllers h5 {
  color: #4b4b4b;
}

.main.even-section .line-col {
  background-color: rgba(0, 0, 0, 0.05);
}

.main.even-section .social-links__item a {
  color: #fff;
  border-color: #fff;
}

.main.even-section .scroll-message span {
  color: #4b4b4b;
}

.main.even-section .js-open-menu-btn span {
  color: #4b4b4b;
}

.main.even-section #heroes .logo-mob picture {
  display: none;
}

.main.even-section #heroes .logo-mob picture.even-logo {
  display: inherit;
}

.main.even-section #heroes .heroes__item .section-content .bottom-content h5 {
  color: #4b4b4b;
}

#fullpage {
  position: relative;
  max-width: 1780px;
  width: calc(100% - 140px);
  margin-left: auto;
}

.section-img {
  height: 100%;
  position: absolute;
  justify-content: flex-end;
  top: 0;
  right: 0;
  z-index: 3;
}

.section-img img {
  max-height: 100vh;
}

.section-img > img {
  height: 100%;
  max-width: unset;
}

.section {
  max-width: 1780px;
  padding: 45px 40px;
  background-color: #042a8c;
  overflow: hidden;
}

.section:nth-child(even) {
  background-color: #fcfcfc;
}

.section:nth-child(even) .scroll-message span {
  color: #000;
}

.section:nth-child(even) .menu-toggler-btn span {
  color: #4b4b4b;
}

.section:nth-child(even) .big-text {
  color: #f3f9fb;
}

.section .pp-tableCell {
  display: block;
  width: unset;
}

.section::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 0;
}

.section-top {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 10;
}

.language-box {
  width: 88px;
  height: 45px;
  background: #fff;
  border-radius: 100px;
  display: none;
  align-items: center;
  justify-content: center;
  margin-right: 50px;
}

.language-box a {
  width: 38px;
  height: 38px;
  background: #042a8c;
  line-height: 39px;
  display: block;
  font-size: 13px;
  font-weight: bold;
  color: #fff;
  border-radius: 50%;
  text-align: center;
}

.language-box a:not(:last-child) {
  margin-right: 4px;
}

.language-box a.active {
  background-color: #14b1e7;
}

.scroll-message {
  position: absolute;
  left: 100px;
  bottom: 65px;
}

.scroll-message span {
  display: inline-block;
  color: #fff;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 900;
  margin-bottom: 15px;
  transform: rotate(-90deg);
}

.big-text {
  position: absolute;
  top: 50%;
  left: 120px;
  font-size: 350px;
  font-weight: bold;
  color: #193fa0;
  z-index: -1;
  text-transform: uppercase;
  user-select: none;
  pointer-events: none;
}

.section-content {
  display: flex;
  position: relative;
  z-index: 2;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  margin-left: 150px;
  color: #4b4b4b;
  max-width: 500px;
}

.section-content h3 {
  margin-bottom: 30px;
  font-size: 46px;
  text-transform: uppercase;
}

.section-content p {
  margin-bottom: 20px;
  font-size: 16px;
  font-family: "Roboto", "Arial", sans-serif;
  line-height: 1.5;
  font-weight: normal;
}

.section-content a {
  color: #ed0677;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 900;
}

.logo-mob {
  display: none;
}

.section-menu {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 4;
}

.section-menu li:not(:last-child) {
  margin-right: 35px;
}

.section-menu a {
  font-size: 15px;
  text-transform: uppercase;
  color: #fff;
}

.menu-mob.menu-toggler-btn {
  display: none;
}

.home::before {
  background-image: url("../img/main-bg.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top left 10%;
}

.home-content {
  margin-left: 50px;
  margin-top: 25vh;
}

.home-content h1 {
  max-width: 500px;
  color: #fff;
  font-size: 48px;
  font-weight: 900;
}

.home-content .img-wrapper {
  justify-content: flex-start;
  position: relative;
  z-index: 4;
}

.fullpage-menu {
  position: absolute;
  max-width: 1780px;
  width: calc(100% - 140px);
  height: 100vh;
  top: 0;
  right: 0;
  transform: translateY(-150%);
  padding: 45px 40px;
  background-color: #042a8c;
  overflow: hidden;
